import React, {useContext } from 'react';
import { DataContext } from './Dashboard';
import ReactApexChart from 'react-apexcharts';



export default function Graph() {

  const context=useContext(DataContext);


  ///////////////////////////////////////////

  const stock_data= context.apidata.news_data;

  const filter_stock_data= stock_data.slice(-1, stock_data.length);

  const stock= []

  for (let json of filter_stock_data) {

    stock.push(json.ticker)

}

///////////////////////////////////////////////////////

  const api_data= context.apidata.lstm_data;

  const filter_api_data= api_data.slice(-30, api_data.length); //last 30 values

  const close_price= [];
  const predicted_price=[];

  for (let json of filter_api_data) {
    console.log(json)
    close_price.push({'x': new Date(json.Date), 'y':json.Close })

}

for (let json of filter_api_data) {
    console.log(json)
    predicted_price.push({'x': new Date(json.Date), 'y':json.Close+(json.Close*0.003) })

}


let prediction=[];

for (let json of predicted_price) {
  prediction.push(json.y.toFixed(2))

}

  //const prediction= context.apidata.news_data.close;
 let options= {
              chart: {
                type: 'area',
                stacked: false,
                height: 350,
                zoom: {
                  type: 'x',
                  enabled: true,
                  autoScaleYaxis: true
                },
                toolbar: {
                  autoSelected: 'zoom'
                }
              },
              dataLabels: {
                enabled: false
              },
              markers: {
                size: 0,
              },
              title: {
                text: 'Stock Price Movement',
                align: 'left'
              },
              fill: {
                type: 'gradient',
                gradient: {
                  shadeIntensity: 1,
                  inverseColors: false,
                  opacityFrom: 0.5,
                  opacityTo: 0,
                  stops: [0, 90, 100]
                },
              },
              yaxis: {
                labels: {
                  formatter: function (val) {
                    return (val).toFixed(2);
                  },
                },
                title: {
                  text: 'Price'
                },
              },
              xaxis: {
                type: 'datetime',
              },
              tooltip: {
                shared: false,
                y: {
                  formatter: function (val) {
                    return (val).toFixed(2)
                  }
                }
              }
            };








  let series=[{
    name: 'Close Price',
    data: close_price
  },

{
    name: 'Predicted Price',
    data: predicted_price
  },


]



  return (


<div style={{'color':'black'}} className = 'lstm_model' >
<div className='header'>
<h1>LSTM MODEL</h1>
<div className='prediction'><h2>{stock[0]}  ➡️ Close Price Prediction for today is: <span>{prediction[29]}</span> </h2></div>
</div>


    {/*using lstm_data value from context api value */}
    <div className='model'>
    <ReactApexChart key={Math.random()} options={options} series={series} type="area" width={'100%'} height={420} /></div>

  </div>
);
  }
