import React,{useContext,useState,useEffect} from 'react';
import {BiSearchAlt} from 'react-icons/bi';
import {FiBell} from 'react-icons/fi';
import {BsFillCaretDownFill,BsFillMenuButtonFill} from 'react-icons/bs';
import avatar from '../assets/vivek.jpg';
import { DataContext } from './Dashboard';


export default function Navbar() {

  const info= {
  AAPL:{country:"america", exchange:"NASDAQ"},
  TCS:{country:"india", exchange:"BSE"},
  INFY:{country:"india", exchange:"BSE"}
};

  const context=useContext(DataContext);


  const [text,setText]=useState(null);
  useEffect(()=>{
    //will be executed when page is loaded
    setText('AAPL')
    setTimeout(()=>{
      document.getElementById('submit')?.click();
    },[1000])

  },[])
  //const [count,setCount]=useState(0)

  //const updatedContext=(key,data)=>{
  //  context.setapiData({...context.apidata,[key] :data})
  //}

  const handleSubmit=async(e)=>{
    e?.preventDefault();
    var formateres5=[];
    var formateres4=[];
    var formateres3=[];
    var formateres2=[];
    var formateres1=[];
    var formateres0=[];


    try{
      let res0=await fetch("https://stockanalysisapi-xyy5p4vcpa-ew.a.run.app/prediction?ticker="+text)
      formateres0=await res0.json()
    }
    catch{}

    try{
      let res1=await fetch("https://stockanalysisapi-xyy5p4vcpa-ew.a.run.app/twitter?ticker="+text)
      formateres1=await  res1.json()
    }
    catch{}

    try{
      let res2=await fetch("https://stockanalysisapi-xyy5p4vcpa-ew.a.run.app/technical?ticker="+text)
      formateres2=await res2.json()
    }catch{}


    try{
      let res3=await fetch("https://stockanalysisapi-xyy5p4vcpa-ew.a.run.app/newslist?ticker="+text)
      formateres3=await res3.json()

    }catch{}

    try{
      let res4=await fetch("https://stockanalysisapi-xyy5p4vcpa-ew.a.run.app/summary?symbol="+text+"&exchange="+info[text].exchange+"&country="+info[text].country+"&interval=1d")
      formateres4=await res4.json()
    }catch{}


    try{
      let res5=await fetch("https://stockanalysisapi-xyy5p4vcpa-ew.a.run.app/recommendation?ticker="+text)
      formateres5=await res5.json()
    }
    catch{

    }



    context.setapiData({...context.apidata,'advice_data':formateres5,'indicators_data':formateres4, 'news_data':formateres3,'lstm_data':formateres2,'twitter_data':formateres1, 'prediction_data':formateres0})
    }




  const toggleSidebar=()=>{
    if(window.innerWidth>760){
      return;
    }
    let element=document.getElementsByClassName('sidebar');
    console.log("eleme",element);
    if(element){

        if(element[0].style.left=='-16rem' || !element[0].style.left){
          element[0].style.left='0rem';
        }
        else{
          element[0].style.left='-16rem'
        }

    }

  }
  return (
    <div className='navbar'>
      <span onClick={toggleSidebar} id='menu'><BsFillMenuButtonFill/></span>
        <div className= 'title'>
            <h2>DASHBOARD</h2>
            <span>Stocks Predictions Everyday</span>
        </div>



        <div className= 'container'>

        <form onSubmit={handleSubmit}  method="GET">



                <div className='search'>
                     <BiSearchAlt/>
                     <input type='text' onChange={e=>setText(e.target.value)} placeholder='' name="search_query"/>
                     <select onChange={e=>setText(e.target.value)} >
                      <option value="">Search...</option>
                      <option value="AAPL">AAPL</option>
                      <option value="INFY">INFY</option>
                     <option value="TCS">TCS</option>

</select>

                </div>

                <input id='submit' className='button' type= 'submit' value='PREDICT'/>

</form>



                <div className= 'profile'>

                      <FiBell className='bell'/>

                      <div className='image'>
                      <img src={avatar} alt='' />
                      </div>

                      <div className='profile__name'>
                      <h4>Vivek Jain</h4>
                      <h6>Admin</h6>
                      </div>

                      <BsFillCaretDownFill/>


                </div>


      </div>



  </div>
  );
}
