import { MdLogout,MdEmojiPeople,MdClose} from "react-icons/md";
import { ImBlogger2 } from "react-icons/im";
import rocket from "../assets/rocket.png";


export default function Sidebar() {
  useEffect(()=>{
    window.addEventListener('resize',function(){
      if(window.innerWidth>750){
        let element=document.getElementsByClassName('sidebar');
        if(element){
       if(element[0].style.left==='-16rem'){
        element[0].style.left='0rem';
      }


  }
      }
    })
  },[])
  const links1= [
    {
      text:'Home',
      icon: FiShoppingBag,
      active:true,
    },

    {
      text: 'Portfolio Report',
      icon: AiOutlineStock,
    },

    {
      text: 'Fear & Greed',
      icon: AiFillTwitterCircle,
    },

    {
      text: 'Blog',
      icon: ImBlogger2,
    },

    {
      text: 'About Us',
      icon: MdEmojiPeople,
    },

  ];

const links2= [

  {
    text: 'Contact Us',
    icon: BiHelpCircle,
  },

  {
    text: 'Settings',
    icon: FiSettings,
  },

  {
    text: 'Logout',
    icon: MdLogout,
  },

];

const toggleSidebar=()=>{
  if(window.innerWidth>760){
    return;
  }
  let element=document.getElementsByClassName('sidebar');
  console.log("eleme",element);
  if(element){

      if(element[0].style.left==='-16rem' || !element[0].style.left){
        element[0].style.left='0rem';
      }
      else{
        element[0].style.left='-16rem'
      }

  }

}

  return (
    <div onClick={toggleSidebar} className= 'sidebar'>
      <span id='close'><MdClose/></span>
<div className='brand'>StockAnalysis
<img src="logo.png" alt="logo"/></div>
  <div className='links'>
  <Links links={links1} />
   <Links links={links2} />
      </div>



<div className='pro'>
      <div className='image'>

        <img src={rocket} alt='rocket' />
        </div>

      <h3> DASHBOARD PRO</h3>

      <h4>Get access to all features:</h4>

      <button className='button'>Get Pro!</button>

      </div>

    </div>
  );
}
