import React, {useContext } from 'react';
import { DataContext } from './Dashboard';


export default function Advice() {

const context=useContext(DataContext);

  const api_data= context.apidata.advice_data;


  const filter_api_data= api_data?.slice(-6, api_data.length) || []; //last 5 advice

  const dates= [];
  const analyst=[];
  const advice=[];
  const target=[];
  const stock=[];
  const source=[];
  const url=[];



  for (let json of filter_api_data) {

    dates.push(json.date)

}

for (let json of filter_api_data) {

    analyst.push(json.analyst)

}

for (let json of filter_api_data) {

    advice.push(json.advice)

}

for (let json of filter_api_data) {

    target.push(json.target)

}

for (let json of filter_api_data) {

    stock.push(json.ticker)

}

for (let json of filter_api_data) {

    source.push(json.source)

}

for (let json of filter_api_data) {

    url.push(json.url)

}


  return (
    <div className='advice'>

<div className="table-users">
   <div className="header"><h1>{stock[0]} Recommendations </h1></div>

   <table cellSpacing="0">
      <tr>
         <th>Date</th>
         <th>Analyst</th>
         <th>Advice</th>
         <th>Target</th>
         <th>Source</th>



      </tr>





      <tr>
         <td>{dates[5]}</td>
         <td className='analyst'>{analyst[5]}</td>
         <td>{advice[5]}</td>
         <td className='target' style={{color:target[6]==='Long' || 'Buy' || 'Hold'?'#125D98':'red'}}>{target[5]}</td>
          <td className='newsdata'><a href={url[5]} target="_blank" rel="noopener noreferrer"> {source[5]} </a> </td>

      </tr>

      <tr>
         <td>{dates[4]}</td>
         <td className='analyst'>{analyst[4]}</td>
         <td>{advice[4]}</td>
         <td className='target' style={{color:target[6]==='Long' || 'Buy' || 'Hold'?'#125D98':'red'}}>{target[4]}</td>
          <td className='newsdata'><a href={url[4]} target="_blank" rel="noopener noreferrer"> {source[4]} </a> </td>


      </tr>

      <tr>
         <td>{dates[3]}</td>
         <td className='analyst'>{analyst[3]}</td>
         <td>{advice[3]}</td>
         <td className='target' style={{color:target[6]==='Long' || 'Buy' || 'Hold'?'#125D98':'red'}}>{target[3]}</td>
          <td className='newsdata'><a href={url[3]} target="_blank" rel="noopener noreferrer"> {source[3]} </a> </td>


         </tr>

      <tr>
         <td>{dates[2]}</td>
         <td className='analyst'>{analyst[2]}</td>
         <td>{advice[2]}</td>
         <td className='target' style={{color:target[6]==='Long' || 'Buy' || 'Hold'?'#125D98':'red'}}>{target[2]}</td>
          <td className='newsdata'><a href={url[2]} target="_blank" rel="noopener noreferrer"> {source[2]} </a> </td>

      </tr>

      <tr>
         <td>{dates[1]}</td>
         <td className='analyst'>{analyst[1]}</td>
         <td>{advice[1]}</td>
         <td className='target' style={{color:target[6]==='Long' || 'Buy' || 'Hold'?'#125D98':'red'}}>{target[1]}</td>
          <td className='newsdata'><a href={url[1]} target="_blank" rel="noopener noreferrer"> {source[1]} </a> </td>

      </tr>

      <tr>
         <td>{dates[0]}</td>
         <td className='analyst'>{analyst[0]}</td>
         <td>{advice[0]}</td>
         <td className='target' style={{color:target[6]==='Long' || 'Buy' || 'Hold'?'#125D98':'red'}}>{target[0]}</td>
         <td className='newsdata'><a href={url[0]} target="_blank" rel="noopener noreferrer"> {source[0]} </a> </td>

      </tr>


   </table>
</div>
    </div>

  );
}
